<template>
  <div
    class="wrap"
    v-loading="!params.data.length"
    :element-loading-background="$STANDARD.loadingBgColor"
  >
    <div ref="chartEl" :style="{width:canvasStyle.width,height:canvasStyle.height}"></div>
    <img
      v-if="params.data.length&&params.options.bgSize"
      class="bg"
      src="../../assets/pie-bg.png"
      alt=""
      :style="{
          left:params.options.center[0],
          top:params.options.center[1],
          width:params.options.bgSize+'px',
          height:params.options.bgSize+'px'
            }"
    >
  </div>

</template>

<script>
  // 引入基本模板
  let echarts = require("echarts/lib/echarts");
  //引入饼状图
  require("echarts/lib/chart/pie");
  // 引入提示框和标题组件
  require("echarts/lib/component/tooltip");
  require("echarts/lib/component/title");
  require("echarts/lib/component/legend");
  export default {
    name: "PieW",
    data() {
      return {
        interval: null,
        rollFlag: false,
        myChart: null
      };
    },
    props: {
      animateFlag: Boolean,
      defaultHigh: {
        type: Number
      },
      params: {
        type: Object,
        default() {
          return {
            data:[],
            canvasStyle: {
              width: "500px",
              height: "500px"
            },
            options: {
              radius: ["30%", "60%"], // 饼图大小
              center: ["50%", "50%"], // 饼图位置，默认垂直水平居中
              length: 60, //线的长度
              length2: 60, //第二条线的长度
              bRichPadding: [30, -50, 0, -50],
              roseType: "area", //玫瑰图配置项，默认为空，不配置则为基本饼图 area || radius
              labelSize: 14,
              borderWidth: 10,
              borderColor: "#fff",
              legend: {
                //图例
                show: true,
                left: 10,
                top: 240,
                itemidth: 14,
                itemHeight: 14,
                orient: "vertical", //horizontal垂直
                fontSize: 14
              },
              tooltip: {
                show: true //
              },
              rich: {
                bfontsize: 16, //文字大小
                perfontsize: 24 //百分比大小
              },
              colors: [], //item的颜色
              rollFlag: false, //tootil是否自动轮播默认不是自动轮播
              itemLabelShow: true, //数据扇区label是否显示
              itemEmphasisLabelShow: true, //高亮时数据扇区label是否显示
              defaultHigh: 0
            }
          };
        }
      }
    },
    created() {
      this.$nextTick(() => {
        this.init();
      });
    },
    mounted() {
      this.init();
      this.$once('hook:beforeDestroy', () => {
        if (this.interval) {
          clearInterval(this.interval);
        }
        this.myChart && this.myChart.dispose();
      })
    },
    methods: {
      init() {
        if (this.myChart) {
          this.myChart.dispose();
        }

        if (this.params.data.length === 0) return;
        let chartData = this.params.data;
        let options = this.params.options;
        let circleRadius = [];
        let a = 0, b = 0;
        b = Number(options.radius[0].substring(0, options.radius[0].length - 1)) - 4;
        a = b - 1;
        circleRadius = [a + '%', b + '%'];

        // 基于准备好的dom，初始化echarts实例
        this.myChart = echarts.init(this.$refs.chartEl);
        this.myChart.setOption({
          title: {
            text: options.title ? options.title : "",
            textStyle: {
              color: "#fff",
              fontSize: "18%"
            },
            x: "center",
            y: "bottom"
          },
          tooltip: {
            show: options.tooltip
              ? options.tooltip.show === undefined
                ? true
                : options.tooltip.show
              : true,
            trigger: "item",
            backgroundColor: this.$STANDARD.tooltipBackgroundColor,
            padding: [10, 20],
            textStyle: {
              color: this.$STANDARD.tooltipColor
            },
            position: options.labelShow === false ? ["33%", "35%"] : null,
            formatter: function (params) {
              return params.name
                ? `${params.marker}${params.name} : ${params.value}`
                : "";
            }
          },
          legend: {
            show: options.legend ? options.legend.show : false,
            selectedMode:
              options.legend.selectedMode === undefined
                ? true
                : options.legend.selectedMode,
            left: options.legend
              ? options.legend.left
                ? options.legend.left
                : "left"
              : "left",
            top: options.legend
              ? options.legend.top
                ? options.legend.top
                : "top"
              : "top",
            itemWidth: options.legend.itemWidth ? options.legend.itemWidth : 14,
            itemHeight: options.legend.itemHeight
              ? options.legend.itemHeight
              : 14,
            textStyle: {
              fontSize: options.legend.fontSize
                ? options.legend.fontSize
                : this.$STANDARD.h4.fontSize,
              color: options.legend ? options.legend.color : this.$STANDARD.legendColor
            },
            icon: "circle",
            itemGap: 15,
            orient: options.legend
              ? options.legend.orient
                ? options.legend.orient
                : "vertical"
              : "vertical"
          },
          // color: color,
          series: [
            {
              name: "分析",
              type: "pie",
              center: options.center || ["50%", "50%"],
              roseType: options.roseType ? options.roseType : "", //玫瑰饼图配置项 area || radius
              radius:
                options.radius === undefined ? ["0%", "60%"] : options.radius,
              minAngle: 6,
              label: {
                normal: {
                  show: false,
                  position: "center",
                  formatter: params => {
                    if (params.name.length > 7) {
                      return `{b|${params.name.slice(0, 6)}\n${params.name.slice(6, params.name.length)}}\n{d|${params.percent}%}`
                    } else {
                      let result = ''
                      result = options.threeLevel ? `{b|${params.value}}\n{b|${params.name}}\n{d|${params.percent}%}`
                        : `{b|${params.name}}\n{d|${params.percent}%}`
                      return result
                    }
                  },
                  rich: {
                    d: {
                      fontSize: options.labelRich ? options.labelRich.d ? options.labelRich.d : this.fontSize(0.22) : this.fontSize(0.22),
                      fontWeight: "bold",
                      lineHeight: options.labelRich ? options.labelRich.dHeight ? options.labelRich.dHeight : this.fontSize(0.4) : this.fontSize(0.4),
                      color: "#e03562"
                    },
                    b: {
                      fontSize: options.labelRich ? options.labelRich.b ? options.labelRich.b : this.fontSize(0.2) : this.fontSize(0.2),
                      fontWeight: "bold",
                      lineHeight: options.labelRich ? options.labelRich.bHeight ? options.labelRich.bHeight : this.fontSize(0.4) : this.fontSize(0.4),
                      color: (params) => {
                        return Array.isArray(options.colors[params.dataIndex])
                          ?
                          options.colors[params.dataIndex][0]
                          :
                          options.colors[params.dataIndex]
                      }
                    }
                  }
                },
                emphasis: {
                  show: true
                }
              },
              itemStyle: {
                borderColor: options.borderColor
                  ? options.borderColor
                  : "transparent",
                borderWidth: options.borderWidth ? options.borderWidth : 0,
                color: (params) => {
                  return Array.isArray(options.colors[params.dataIndex])
                    ?
                    {
                      type: 'linear',
                      x: 0,
                      y: 0,
                      x2: 0,
                      y2: 1,
                      colorStops: [{
                        offset: 0, color: options.colors[params.dataIndex][0] // 0% 处的颜色
                      }, {
                        offset: 1, color: options.colors[params.dataIndex][1]// 100% 处的颜色
                      }],
                      global: false // 缺省为 false
                    }
                    :
                    options.colors[params.dataIndex]
                }
              },
              labelLine: {
                length: options.length ? options.length : 10,
                length2: options.length2 ? options.length2 : 10
              },
              data: chartData
            },
            {
              type: 'pie',
              hoverAnimation: false,
              center: options.center || ["50%", "50%"],
              radius: circleRadius,
              data: [100],
              label: {
                show: false
              },
              itemStyle: {
                normal: {
                  color: "#0e90cc"
                },
                emphasis: {
                  show: false
                }
              }
            }
          ]
        });
        this.rollFlag = this.params.options.rollFlag;
        this.myChart.on("click", params => {
          if (params.componentType === "series") {
            this.$emit("departmentClick", params.name);
            params.allData = {
              data: this.params.data,
              params: params
            };
            this.$emit("canvasClick", params.allData);
          } else {
            this.$emit("departmentClick", params.value);
          }
        });
        if (this.rollFlag) {
          this.chartMove();
          this.myChart.on("mouseover", params => {
            // 鼠标移到内部圆环上时不触发事件
            if (params.seriesIndex === 1) return;
            this.rollFlag = false;
            if (params.dataIndex === this.app.currentIndex) {
              // 序号相同不取消高亮
              return;
            } else {
              // 取消之前高亮的图形
              this.myChart.dispatchAction({
                type: "downplay",
                dataIndex: this.app.currentIndex
              });
            }
          });
          this.myChart.on("mouseout", params => {
            this.rollFlag = true;
          });
        } else {
          let defaultHigh = this.defaultHigh;
          if (defaultHigh !== "" && defaultHigh !== undefined) {
            this.myChart.dispatchAction({
              type: "highlight",
              seriesIndex: 0,
              dataIndex: defaultHigh
            });
            let _this = this;
            _this.myChart.on("mouseover", function (params) {
              var dataLen = chartData.length;
              for (var i = 0; i < dataLen; i++) {
                if (i != params.dataIndex) {
                  _this.myChart.dispatchAction({
                    type: "downplay",
                    seriesIndex: 0,
                    dataIndex: i
                  });
                }
              }
            });
            _this.myChart.on("mouseout", function (params) {
              _this.myChart.dispatchAction({
                type: "highlight",
                seriesIndex: 0,
                dataIndex: defaultHigh
              });
            });
          }
        }
      },
      // 控制tootile 的自动轮播
      chartMove() {
        this.app = {};
        this.app.currentIndex = -1;
        if (this.interval) {
          clearInterval(this.interval);
        }
        this.interval = setInterval(() => {
          let dataLen = this.params.data.length;
          if (this.rollFlag) {
            // 取消之前高亮的图形
            this.myChart.dispatchAction({
              type: "downplay",
              seriesIndex: 0,
              dataIndex: this.app.currentIndex
            });
            this.app.currentIndex = (this.app.currentIndex + 1) % dataLen;
            // 高亮当前图形
            this.myChart.dispatchAction({
              type: "highlight",
              seriesIndex: 0,
              dataIndex: this.app.currentIndex
            });
            this.myChart.dispatchAction({
              type: "showTip",
              seriesIndex: 0,
              dataIndex: this.app.currentIndex
            });
          }
        }, 2000);
      },
      fontSize(res) {
        let docEl = document.documentElement,
          clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      },
    },
    computed: {
      sum() {
        let sum = 0;
        this.params.data.forEach(item => {
          sum += item.value;
        });
        return sum;
      },
      ishow() {
        if (
          this.params.options.labelShow === false ||
          this.params.options.showsum === false
        ) {
          return false;
        } else {
          return true;
        }
      },
      canvasStyle() {
        return {
          width: this.params.canvasStyle
            ? this.params.canvasStyle.width
            : "100%",
          height: this.params.canvasStyle
            ? this.params.canvasStyle.height
            : "100%"
        };
      }
    },
    watch: {
      'params.data': {
        handler(newVal, oldVal) {
          this.init();
        },
        deep: true
      }
    }
  };
</script>

<style lang="stylus" scoped>
  .wrap
    position relative
    width 100%
    height 100%
    .bg
      position absolute
      z-index -1
      transform translate(-50%, -50%)
</style>
